	methods: {
		search(){
			uni.navigateTo({
				url:'../../pagesMy/Search/Search'
			})
		},
		// 投票
		voteCom(item){
			if(item.vote_status){
				item.vote_status = false
				item.vote++
				this.$api.LuDingJiVote({
					token: uni.getStorageSync("token"),
					user_id: uni.getStorageSync("userId"),
					id:item.id
				}).then(res=>{
					uni.showToast({
					    title: res.data.msg,
					    duration: 1000,
						icon:'none'
					});
				})
			}else{
				uni.showToast({
				    title: '每天只能投一票哦！',
				    duration: 1000,
					icon:'none'
				});
			}
		},
		// 跳转路顶记详情
		LuDetails(id){
			uni.navigateTo({
				url:'../../pagesMy/LuDingJiDetails/LuDingJiDetails?id='+id
			})
		},
		// 跳转个人主页
		MyInfo(id){
			uni.navigateTo({
				url:'../../pagesMy/MyInfo/MyInfo?id='+id
			})
		},
		tab(ind){
			this.n = ind
			uni.pageScrollTo({
			    scrollTop: 0,
			    duration: 0
			});
			
		},
		getData1(){
			this.$api.NewsRoadShowsProject({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				type:1,
				page:this.page1
			}).then(res=>{
				if(res.data.code === '200'){
					if(this.page1 === 1){
						if(res.data.data.length>=20){
							this.list1 = res.data.data
							this.allowOne = true
							this.loadingOne = false
						}else{
							this.list1 = res.data.data
							this.allowOne = false
							this.loadingOne = false
							this.showTitle1 = true
						}														
					}else{
						if(res.data.data.length>=20){
							this.list1 = this.list1.concat(res.data.data)
							this.allowOne = true
							this.loadingOne = false
						}else{
							this.list1 = this.list1.concat(res.data.data)
							this.allowOne = false
							this.loadingOne = false
							this.showTitle1 = true
						}	
					}
						
					
				}
				
			})
		},
